.container-search-and-select {
    width: 100%;
    margin-left: 7%;
    margin-right: 7%;
    /* margin-top: 3rem; */
  
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap; 
}  

.select {
    width: 20%;
    margin: 4rem 0 2rem;
  
    padding: 1.5rem 1.75rem;
    border-radius: 3rem;
    background: #1f2123;
    box-shadow: 5px 5px 7px #1c1d1f, -5px -5px 7px #222527;
}

.select select {
    position: relative;
    width: 100%;
    border: none;
    font-size: 1.5rem;
    font-family: var(--font-raleway);
    font-weight: 500;

    outline: none;
    color: #a1a1a1;
    background: #1f2123;
}
  
.select select option:hover {
    background: red;
}

.empty {
    width: 100%;
    margin-top: 3rem;
  
    display: flex;
    justify-content: center;
    align-items: center;
}
  
.empty h2 {
    font-size: 1.25rem;
    color: #f9d3b4;
    font-family: var(--font-raleway);
}
